.App-body {
  background-color: #87BBA2;
  min-height: 90vh;
  font-size: calc(10px + 2vmin);
  color:  #3B6064;
}
:root {
	--primary-color: white
}
.logo{justify-content: center;
	text-align: center;
	padding: 10px;
}
.graphic{
	float:right;
	padding-right: 15%;}
.slogan{
	color: #3B6064;
}
.emailinput{
	background-color: #F2EFEF;
	border-radius: 25px;
	padding-top:10px;
	padding-bottom: 10px;
	padding-left:20px ;
	padding-right:20px ;
	margin-left: 6%;
}
.mailinglistbtn{
 	background-color: #E0BE36;
	border: none;
	color: #3B6064;
	padding: 12px;
	padding-left: 20px;
	padding-right: 25px;
	text-align: center;
	text-decoration: none;
	font-size: 16px;
	margin: 2px 2px;
	cursor: pointer;
	border-radius: 25px;
	clear: both;
	margin-left:7%;
}
.quizlink{
	margin-left: 8%;
	color: #3B6064;
}
.mailinglist{
	margin-left: 20%;
	clear: left;
	padding-top:5% ;
}

.survey{
	font-size: 15px;
	font-weight: bold;
	text-decoration: underline;
}

.content-container{
	clear: both;
}

footer {
    padding: 30px 0;
    clear: both;
    font-size: 12px;
    color: black;
    text-align: center;
    background: #3B6064;
    border-top: 8px solid #E0BE36;
    height: 50px;
    width: 100%;
  }

  .SocialIcons{
	 font-size: xx-large;
	 color: whitesmoke;
	 margin: 5px;
  }